<template>
  <div id="app">
    <div class="moor-item">
      <label>Title:</label> 
      <moor-hello-world :color="color" :msg="msg"></moor-hello-world>
    </div>

    <div class="moor-item">
      <label>Button:</label> 
      <moor-button class="moor-btn" @click="onClick"></moor-button>
      <moor-button class="moor-btn" @click="onClick" bgcolor="#409eff" color="#fff"></moor-button>
      <moor-button class="moor-btn" @click="onClick" bgcolor="#67c23a" color="#fff"></moor-button>
      <moor-button class="moor-btn" @click="onClick" bgcolor="#e6a23c" color="#fff"></moor-button>
      <moor-button class="moor-btn" @click="onClick" bgcolor="#f56c6c" color="#fff"></moor-button>
    </div>
    
    <div class="moor-item">
      <label>Select: </label>
      <moor-select 
        class="moor-select"
        label="label"
        value="value"
        v-model="selectValue"
        :options="options">
      </moor-select>
    </div>

    <div class="moor-item">
      <label>Input: </label>
      <moor-input
      v-model="input1"
      placeholder="">
      </moor-input>

      <moor-input
        v-model="input2"
        placeholder="">
      </moor-input>

      <moor-input
        placeholder=""
        :disabled="inputDisabled">
      </moor-input>
    </div>

    <div class="moor-item">
      <label>Switch: </label>

      <moor-switch
      v-model="lightSwitch">():</moor-switch>

      <moor-switch
      v-model="switchLight">():</moor-switch>
    </div>
    
    <moor-button class="moor-btn" @click="handlePrint"></moor-button>
  </div>  
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      // HelloWorld
      msg: 'Welcome to moor UI!',
      color: 'red',
      // Select
      selectValue: '',
      options: [
        {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }
      ],
      // input
      input1: '',
      input2: '',
      inputDisabled: true,
      // switch
      lightSwitch: false,
      switchLight: true
    }
  },
  methods: {
    onClick: function (e) {
      console.log('click', e);
    },
    handlePrint() {
      console.log('selectValue', this.selectValue);
      console.log('input1:', this.input1);
      console.log('input2:', this.input2);
    }
  },
  watch: {
    lightSwitch: newValue => console.log('', newValue),
  }
}
</script>

<style lang="scss">
.moor-select, .moor-btn, .moor-switch, .moor-input {
  margin: 10px 6px;
}
.moor-item {
  display: flex;
  align-items: center;

  label {
    width: 100px;
    display: inline-block;
  }
}
</style>



